* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100vh;
}

body {
  background-color: #f8bbd0;
}

.App {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container {
  background-color: whitesmoke;
  width: 40%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 25px;
  padding: 40px 20px;
}

.container button {
  padding: 25px;
  margin: 30px;
  margin-top: 35px;
  background-color: #f8bbd0;
  border-radius: 10px;
  border: none;
  font-size: 20px;
  transition: 1s ease;
}

.container button:hover {
  background-color: #9e9e9e;
}

.container button:focus {
  outline: none;
}

@media (max-width: 770px) {
  .container {
    width: 80%;
    height: 50%;
  }
  .container button {
    padding: 10px;
    height: 200px;
  }
}
